"use client";

import { useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Link from "next/link";
import HelpDialog from "@/components/dialogs/HelpDialog";
import SettingsDialog from "@/components/dialogs/SettingsDialog";
import ConfirmDialog from "@/components/dialogs/ConfirmDialog";
import Toast from "@/components/feedback/Toast";

export default function HomePageClient() {
  const [showHelp, setShowHelp] = useState(false);
  const [showSettings, setShowSettings] = useState(false);
  const [showConfirm, setShowConfirm] = useState(false);
  const [toastMessage, setToastMessage] = useState("");

  const games = [
    { name: "🎵 Rhyme Time", path: "/games/rhyme-time", color: "from-pink-500 to-rose-500" },
    { name: "🧩 Sentence Shuffle", path: "/games/sentence-shuffle", color: "from-indigo-500 to-blue-600" },
    { name: "🧠 Grammar Guru", path: "/games/grammar-guru", color: "from-green-400 to-emerald-500" },
  ];

  const handleConfirm = async () => {
    // כאן את יכולה לחבר לשרת אמיתי / API בעתיד
    setToastMessage("✅ אישרת את תנאי השימוש בהצלחה!");
    setShowConfirm(false);
  };

  return (
    <div className="flex flex-col items-center justify-center text-center py-16 space-y-10">
      {/* כותרת עליונה */}
      <motion.h1
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.8 }}
        className="text-5xl font-extrabold text-indigo-700 drop-shadow-md"
      >
        🎓 Welcome to English Learning Games
      </motion.h1>

      {/* תפריט עליון עם אייקונים */}
      <div className="flex gap-8 text-gray-700 text-2xl">
        <button onClick={() => setShowHelp(true)} title="עזרה" className="hover:text-indigo-600 transition">
          ❔
        </button>
        <button onClick={() => setShowSettings(true)} title="הגדרות" className="hover:text-indigo-600 transition">
          ⚙️
        </button>
        <button onClick={() => setShowConfirm(true)} title="תנאי שימוש" className="hover:text-indigo-600 transition">
          📜
        </button>
      </div>

      {/* כרטיסי משחקים */}
      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 0.2, duration: 0.6 }}
        className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 px-4 mt-6 w-full max-w-6xl"
      >
        {games.map((game) => (
          <motion.div
            key={game.name}
            whileHover={{ scale: 1.05 }}
            className={`bg-gradient-to-br ${game.color} rounded-3xl shadow-lg text-white p-8 flex flex-col justify-between hover:shadow-2xl transition`}
          >
            <div className="text-3xl font-bold mb-4">{game.name}</div>
            <Link href={game.path}>
              <motion.button
                whileTap={{ scale: 0.95 }}
                className="w-full py-3 rounded-xl bg-white text-gray-900 font-semibold hover:bg-gray-100 transition"
              >
                התחילי לשחק 🎮
              </motion.button>
            </Link>
          </motion.div>
        ))}
      </motion.div>

      {/* פוטר */}
      <footer className="text-gray-500 text-sm pt-12">
        © 2025 English Learning Platform | Built with ❤️ by Team Experis
      </footer>

      {/* דיאלוגים */}
      <AnimatePresence>
        {showHelp && <HelpDialog open={showHelp} onClose={() => setShowHelp(false)} />}
        {showSettings && <SettingsDialog open={showSettings} onClose={() => setShowSettings(false)} />}
        {showConfirm && (
          <ConfirmDialog
            open={showConfirm}
            onClose={() => setShowConfirm(false)}
            onConfirm={handleConfirm}
            message="האם את מאשרת את תנאי השימוש באתר?"
          />
        )}
        {toastMessage && <Toast message={toastMessage} />}
      </AnimatePresence>
    </div>
  );
}
